body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  width: 100%;
  object-fit: cover;

  background-position: center;
  text-align: center;
  background-image: linear-gradient(
    rgb(0, 36, 0),
    rgba(31, 31, 0, 0.807),
   rgb(0, 0, 0)
  );
}

* {
  margin: 0%;
  padding: 0%;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;

  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#title{
    width: 30%;
    height: 2rem;
    margin-top: 1rem;
    border: none;
    outline: none;
}
#des{
    width: 30%;
    height: 2rem;
    margin-top: 1rem;
    border: none;
    outline: none;
}
#sent{
    margin-top: 1rem;
    width: 10%;
    height: 2rem;
    border: none;
    margin-bottom: 1rem;
    outline: none;
}
#titles{
    color: white;
    background-color: rgba(37, 34, 34, 0.944);
    padding: 2rem;
}

.block-data{
    box-shadow: 0.2 0.2 0.7 0.7 rgba(0, 0, 0, 0.674);
    border-radius: 2rem;
    background-color: rgba(255, 255, 255, 0.114);
    margin-top: 1rem;
    margin-left: 10%;
    margin-right: 10%;
    padding: 2rem;
   z-index: 1;
}


@media screen and (max-width:900px) {

    #title{
        width: 80%;
        height: 2rem;
        margin-top: 1rem;
        border: none;
        outline: none;
    }
    #des{
        width: 80%;
        height: 2rem;
        margin-top: 1rem;
        border: none;
        outline: none;
    }
    #sent{
        margin-top: 1rem;
        width: 40%;
        height: 2rem;
        border: none;
        margin-bottom: 1rem;
        outline: none;
    }
    
}
.block-data:hover{
    background-color: rgba(255, 255, 255, 0.342);
}
.block-data h4{
    color: rgb(255, 255, 255);

}

.block-data p{
    color: rgb(255, 255, 255);
}
.navigation{
    background-color: rgb(36, 36, 36);
}

.navigation h1{
    color: white;
}
